// src/hooks/useProductDetails.ts
import { useState, useEffect } from "react";
import { api } from "@/services/api";
import { ProviderType, UnifiedProduct } from "@/types/types";

export const useProductDetails = (
	provider: ProviderType,
	productId: string
) => {
	const [product, setProduct] = useState<UnifiedProduct | null>(null);
	const [relatedProducts, setRelatedProducts] = useState<UnifiedProduct[]>([]);
	const [ratings, setRatings] = useState<any[]>([]);
	const [loading, setLoading] = useState(true);
	const [error, setError] = useState<string | null>(null);
	const [isMock, setIsMock] = useState(false);

	// Mock data
	const MOCK_PRODUCT = {
		id: "mock-product",
		name: "Produto Demonstrativo",
		price: "199.99",
		description: "Descrição resumida do produto demonstrativo",
		fullDescription: "Descrição completa do produto demonstrativo...",
		provider: "BRASILIAN" as ProviderType,
		averageRating: 4.5,
		ratingCount: 12,
		image: "/placeholder-product.jpg",
		category: "eletronicos",
	};

	const MOCK_RATINGS = [
		{
			id: 1,
			user: "Cliente Satisfeito",
			rating: 5,
			comment: "Produto excelente!",
			date: "2023-10-15",
		},
	];

	const fetchData = async () => {
		try {
			setLoading(true);
			setIsMock(false);

			// Buscar produto principal
			const productData = await api.providers.getById(provider, productId);
			setProduct(productData);

			// Buscar produtos relacionados (filtro por categoria)
			const allProducts = await api.providers.getAll();
			const related = allProducts
				.filter(
					(p) => p.category === productData?.category && p.id !== productId
				)
				.slice(0, 4);
			setRelatedProducts(related);

			// Buscar avaliações (simulado - em produção seria uma chamada API real)
			setRatings(MOCK_RATINGS);
		} catch (err) {
			console.error("Erro ao carregar dados:", err);
			setIsMock(true);
			setProduct(MOCK_PRODUCT as UnifiedProduct);
			setRelatedProducts([]);
			setRatings(MOCK_RATINGS);
			setError("Falha ao carregar detalhes do produto");
		} finally {
			setLoading(false);
		}
	};

	useEffect(() => {
		if (productId) {
			fetchData();
		}
	}, [productId]);

	return {
		product,
		relatedProducts,
		ratings,
		loading,
		error,
		isMock,
		refetch: fetchData,
	};
};
